body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*  */

/* font-family: 'Inter', sans-serif; */

*{
    font-family: 'Roboto', bold;
}

.landing{
  display: flex;
  height: 500px;
  background-color: #2d2e32;
  color: aliceblue;
}

.landingText span{
  font-size: 3em;
  padding-top: 260px;
  position: absolute;
  padding-left: 100px;
}

.landingText p{
  margin: auto;
  padding-top: 330px;
  padding-left: 100px;
}

.landing img{
  height: 500px;
  position: absolute;
  right: 5%;
}

.aboutme{
  height: 700px;
  background-color: #25262a;
  display: flex;
}
.uspbox{
  display: flexbox;
  padding-top: 3%;
}
.titleProjects h2{
  padding-left: 105px;
  padding-bottom: 0px;
}
.keybox{
  background-color: #2d2e32;
  width: 700px;
  height: 150px;
  margin-bottom: 2%;
  margin-left: 100px;
  border-radius: 5px;
}
.keybox h2{
  color: #46ba87;
  padding: 20px 0 0 35px;
}
.keybox span{
  padding: 0px 0 0 35px;
  font-weight: 660;
  font-size: 0.9em;
  text-decoration: underline;
  color: #5a5b5c;
 
}
.keybox p {
  padding: 0px 0 0 35px;
  font-weight: 760;
  font-size: 0.9em;
  /* font-family: Roboto; */
}


*{
  color: white;
}
.aboutText{
  margin-right: 5%;
  margin-left: 3%;
  padding-top: 5%;
}

.uspbuttonbox{
  display: flex;
  justify-content: space-between;
  width: 510px;
  position: absolute;
  left: 100px;
  top: 275px;
}
.uspButton:hover{
  cursor: pointer;
}

.uspButton{
  background-color: #4dba87;
  width: 160px;
  height: 35px;
  border-radius: 3px; 
  cursor: pointer;
}
.uspButton:hover{
  background-color: #28694b;
  cursor: pointer;
}

.uspbuttonbox{
  height: 35px;
  line-height: 35px;
  margin: auto;
  text-align: center;
  font-size: 1em;
  color: antiquewhite;

}

/* Projects */

.projects{
  height: 600px;
  width: 100%;
  background-color:#2d2e32;
  display: flex;
  justify-content: center;
}

.projects h1{
  text-align: center;
  margin: auto;
  margin-top: 0%;
  padding-top: 0px;
}
.projects img{
  mix-blend-mode: multiply;
  align-items: center;
  height: 200px;
  /* margin-left: 45%; */
}

.projectsGrid{
  display: flex;
  justify-content: space-around;
  position: absolute;
  margin-top: 15%;
}
.projectid{
  padding-right: 30%;
  width: auto;
  width: 200px;
}

/* font-family: 'Inter', sans-serif; */
/*  */

@media (max-width: 1000px) {
    .navbar ul, .navbar li {
      display: none;
    }
  }
  
  /*  */

.navbar{
    display: flex;
    background-color: #2d2e32;
    justify-content: flex-end;
    height: 65px;
}

.navbar ul{
    list-style: none;
    display: flex;
    justify-content: flex-end;
    padding-left: 100px;
}
.navbar li{
    color: white;
    font-size: 1.2em;   
    padding-right: 8%; 
    padding-top: 1%;
    text-transform: capitalize;
    font-weight: 600;

    white-space: nowrap;
}

.navbar span{
    color: white;
    font-size: 1.2em;   
    padding-right: 2%; 
    padding-top: 1%;
    text-transform: capitalize;
    font-weight: 600;
}
.navbar:hover li {
    cursor: pointer;
}




.footer{
    display: flex;
    justify-content: flex-end;
    background-color: #2d2e32;
    height: 150px;
    padding-top: 0px;
    margin-top: 0px;
    position: relative;
}
.footer h3{
    text-align: left;
    margin: auto;
    position: absolute;
    left: 100px;
    padding-top: 50px;
}


.socials{
    display: flex;
    justify-content: flex-end;
    padding-right: 3%;
}
.socials img{
    height: 55px;
    padding-right: 15%;
    margin: auto;
}

.socials img:hover{
    cursor: pointer;
}
